import axios from 'axios'
const userURL = 'https://shuttheboxapi.azurewebsites.net/v1/';
const state = {
  dbUser: {}
};
const mutations = {
  'SET_DB_USER'(state, dbUser) {
    state.dbUser = dbUser;
  }
}
const actions = {
  addOrUpdateUser: ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      axios.post(userURL + 'user', payload.data, payload.config).then((u) => {
        commit('SET_DB_USER', u.data);
        resolve(u.data);
      }).catch((err) => {
        reject(err);
      });
    });
  },
  getDbUserByUid: ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      axios.get(userURL + 'UserByGuid/' + payload.data.id,null, payload.config).then((u) => {
        commit('SET_DB_USER', u.data);
        resolve(u.data);
      }).catch((err) => {
        reject(err);
      });
    });
  }
}
const getters = {
  dbUser: state => {
    return state.dbUser;
  }
};
export default {
  state,
  mutations,
  actions,
  getters
};



