<template>
  <div class="subscribe">
        <form autocomplete="off" @submit.prevent="validateForm('form-2')"  data-vv-scope="form-2">
    <div class="topheader">
        
    </div>
    
    <div class="body "  v-bind:style="{ height: gameTileHeightSize  + 'px' }">
        <div class="mb-8 logo" style="padding:0 10px 0 10px;">
            <img src="../../assets/Logo.png" style="max-width:200px;" > 
            <span class="version">v{{version}}</span>
        </div>
        <div class="vertical-center" style="padding:0  40px 40px; align-text:justify;">
             
             
               <div >
                    
                    <input 
                    autocomplete="off"
                    name="username"
                     v-validate="'required|min:4|unique'" 
                     :class="{'input': true, 'is-danger': 
                    errors.has('form-2.username') }"
                    data-vv-delay="1000"
                    type="text" 

                    readonly 
                    onfocus="if (this.hasAttribute('readonly')) { this.removeAttribute('readonly');  this.blur();    this.focus();  }"
                      v-model="username"  
                      class="block appearance-none w-full bg-white text-center
                      border border-grey-light hover:border-grey px-2 py-2 
                      rounded shadow" 
                      placeholder="Your Nickname">
                 <span v-show="errors.has('form-2.username')" 
                     class="help is-danger">{{ errors.first('form-2.username') }}</span>
                </div>
                    
                     </div>
    </div>
    <div class="iconFooter">
        <div>
      <button class="btn bg-green hover:bg-green-dark score" 
      type="submit">
      <icon name="user-circle-o"></icon>
      Enter as Guest </button>
    </div>
    
     <div  class="mt-4">
       <router-link to="/login">Login in</router-link>
      |
      <router-link to="/sign-up">Sign Up</router-link>    
    </div>
    </div>
    <div class="footer">

        <div class="flex-no-grow" style="margin-top:5px">
            </div>
            <div class="flex-grow header-logo" style="margin-top:-5px">
               
            </div>
            <div class="flex-no-grow" style="margin-top:5px; ">
            </div>
    </div>
    
    

    

    </form>
  </div>
</template>

<script>
import { version } from "../../../package.json";
import axios from "axios";
import { Validator } from "vee-validate";
import firebase from "firebase";
export default {
  name: "app",
  data: function() {
    return {
      username: "",
      version: version,
      gameTileHeightSize: 0,
      swiperOption: {
        pagination: {
          el: ".swiper-pagination"
        }
      }
    };
  },

  methods: {
    signInAnonymously() {
      let self = this;
      firebase
        .auth()
        .signInAnonymously()
        .then(
          user => {
            self.$toasted.success("Logged on as Guest");

            var u = firebase.auth().currentUser;

            u
              .updateProfile({
                displayName: self.username
              })
              .then(() => {
                // u.getIdToken().then(t => {
                  
                // });

                var payload = {
                    data: {
                      ExternalUniqueID: user.uid,
                      Username: self.username
                    },
                    config: {
                      headers: {
                        Authorization: "bearer " + user.qa,
                        "Content-Type": "application/json"
                      }
                    }
                  };
                  
                  self.$store.dispatch("addOrUpdateUser", payload).then(uU => {
                    self.$toasted.success("Username created " + self.username);
                    self.$router.replace("init");
                  });
              });
          },
          err => {
            self.$toasted.error("Oops.  " + err.message);
          }
        );
    },
    validateForm(scope) {
      this.$validator.validateAll(scope).then(result => {
        if (result) {
          // eslint-disable-next-line
          //alert('Form Submitted!');
          this.signInAnonymously();
        }
      });
    },
    handleWindowResize: function() {
      this.gameTileHeightSize = Math.round(
        _.toSafeInteger(
          document.querySelector(".AppHeight")
            ? document.querySelector(".AppHeight").scrollHeight
            : 0
        ) -
          (_.toSafeInteger(
            document.querySelector(".topheader")
              ? document.querySelector(".topheader").clientHeight
              : 0
          ) +
            _.toSafeInteger(
              document.querySelector(".footer")
                ? document.querySelector(".footer").clientHeight
                : 0
            ) +
            _.toSafeInteger(
              document.querySelector(".iconFooter")
                ? document.querySelector(".iconFooter").clientHeight
                : 0
            ))
      );
    }
  },
  beforeDestroy: function() {
    window.removeEventListener(
      "resize",
      _.debounce(this.handleWindowResize, 500)
    );
  },
  mounted() {
    window.addToHomescreen({
      maxDisplayCount: 1,
      displayPace: 0,
      skipFirstVisit: false
    });
    window.addEventListener("resize", _.debounce(this.handleWindowResize, 500));
    this.handleWindowResize();
  },

  created() {
    const isUnique = value => {
      return axios
        .get("https://shuttheboxapi.azurewebsites.net/v1/users?username=" + value)
        .then(response => {
          // Notice that we return an object containing both a valid property and a data property.
          return {
            valid: response.data.totalCount == 0,
            data: {
              message:
                response.data.totalCount == 0
                  ? "valid user"
                  : "username already exists"
            }
          };
        });
    };

    // The messages getter may also accept a third parameter that includes the data we returned earlier.
    Validator.extend("unique", {
      validate: isUnique,
      getMessage: (field, params, data) => {
        return data.message;
      }
    });
  }
};
</script>

<style>
.slider {
  max-width: 300px;
  margin: 0 auto;
}
.slider ul {
  list-style-type: none;
  text-align: left;
  margin: 0;
  padding: 0;
}
.slider li {
  padding: 0 0 35px;
  font-size: 14px;
}
.slider .icon {
  float: left;
  width: 10%;
  padding-right: 10px;
  min-height: 24px;
}
.slider .content {
  float: left;
  width: 85%;

  min-height: 24px;
  line-height: 24px;
}
.slider li span {
  font-size: 10px;
  color: #666;
  float: right;
}
.slider h2 {
  font-size: 16px;
  padding-bottom: 5px;
}
.slider h3 {
  font-size: 12px;
  color: #666;
  padding-bottom: 20px;
}
.slider p {
  font-size: 12px;
}
</style>



