<template>
  <div class="subscribe">
      <div class="topheader">

      </div>

      <div class="body " v-bind:style="{ height: gameTileHeightSize  + 'px' }">
        <div class="mb-8 logo" style="padding:0 10px 0 10px;">
          <img src="../../assets/Logo.png" style="max-width:200px;">
          <span class="version">v{{version}}</span>
        </div>
        <div class="vertical-center" style="padding:0  40px 40px; align-text:justify;">


          <div>
            <!--TABLE-->
            <table class="table" v-if="board!=null">
              <thead>
                <tr>
                  <th>User</th>
                  <th>Games</th>
                  <th>Points<span>Avg</span></th>
                  <th>Won</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(row, rindex) in board.results">
                  <td>{{ row.Username }}</td>
                  <td>{{ row.NumberGames }}</td>
                  <td>{{ row.AvgPoints }}</td>
                  <td>{{ row.Won }}</td>
                </tr>
              </tbody>
            </table>
          </div>

        </div>
      </div>
      <div class="iconFooter">
       

        <div class="mt-4">
         
        </div>
      </div>
      <div class="footer">

        <div class="flex-no-grow" style="margin-top:5px">
        </div>
        <div class="flex-grow header-logo" style="margin-top:-5px">
          <router-link to="/init">Go Back</router-link>
        </div>
        <div class="flex-no-grow" style="margin-top:5px; ">
        </div>
      </div>





  </div>
</template>

<script>
import { version } from "../../../package.json";
export default {
  name: "app",
  data: function() {
    return {
      username: "",
      version: version,
      gameTileHeightSize: 0,
    };
  },
  computed: {
    board() {
      return this.$store.getters.board;
    }
  },
  methods: {
  
    handleWindowResize: function() {
      this.gameTileHeightSize = Math.round(
        _.toSafeInteger(
          document.querySelector(".AppHeight")
            ? document.querySelector(".AppHeight").scrollHeight
            : 0
        ) -
          (_.toSafeInteger(
            document.querySelector(".topheader")
              ? document.querySelector(".topheader").clientHeight
              : 0
          ) +
            _.toSafeInteger(
              document.querySelector(".footer")
                ? document.querySelector(".footer").clientHeight
                : 0
            ) +
            _.toSafeInteger(
              document.querySelector(".iconFooter")
                ? document.querySelector(".iconFooter").clientHeight
                : 0
            ))
      );
    }
  },
  beforeDestroy: function() {
    window.removeEventListener(
      "resize",
      _.debounce(this.handleWindowResize, 500)
    );
  },
  mounted() {
    window.addEventListener("resize", _.debounce(this.handleWindowResize, 500));
    this.handleWindowResize();
  },
  created() {
    this.$store.dispatch("getBoard");
  }

  
};
</script>

<style>
  .table{
    width:100%;
    padding:0;
    margin:0;
    border:none;
    border-spacing:0;
  }
    .table th:first-child, .table td:first-child {
      text-align: left;
    }
    .table th, .table td {
      padding:5px;
      margin:0;
      }
    .table th{
      border:none;
      border-bottom: solid 1px #999;

    }
  .table tr:nth-child(even) {
    background: #dedede;
  }


    .table th > span{
      font-size:10px;
      color:#808080;
    }
</style>



