import axios from 'axios'
const userURL = 'https://shuttheboxapi.azurewebsites.net/v1/';
const state = {
  board: null
};
const mutations = {
  'SET_BOARD'(state, board) {
    state.board = board;
  }
}
const actions = {
  getBoard: ({ commit }) => {
    return new Promise((resolve, reject) => {
      axios.get(userURL + 'board/').then((u) => {
        commit('SET_BOARD', u.data);
        resolve(u.data);
      }).catch((err) => {
        reject(err);
      });
    });
  }
}
const getters = {
  board: state => {
    return state.board;
  }
};
export default {
  state,
  mutations,
  actions,
  getters
};



